import React, { ReactNode, useEffect, useState } from "react";
import "./styles/Grimoire.css";
import grimoireImage from '../images/Grimoire.png';
import candle from '../images/Candle.png';
import title from '../images/Title.png'
import PostSection from "./PostSection";
import WelcomePost from "./WelcomePost";
import Artifacts from "./Artifacts";
import Wizard from "./Wizard";

type GrimoireProps = {
  children: ReactNode;
};

const Grimoire: React.FC<GrimoireProps> = ({ children }) => {
  const [bookOpened, setBookOpened] = useState(false);
  const [activeSection, setActiveSection] = useState<string | null>(null);

  return (
    <>
      {!bookOpened && (
        <div className="MainContainer">
          <div className="Title"></div>
          <div className="Table">
            <div className="Altart">
              <div className="Candle"></div>
              <img src={grimoireImage} alt="Grimoire" className={`book-img ${bookOpened ? 'book-img-hidden' : ''}`} onClick={() => { setBookOpened(true) }} />
              <div className="Candle"></div>
            </div>
          </div>
        </div>
      )}
      {bookOpened && (
        <div className="modal-overlay"
          onClick={(e) => {
            if ((e.target as HTMLElement).classList.contains('modal-overlay')) {
              setBookOpened(false);
            }
          }}>
          <div className="modal-content">
            <aside className='modal-menu'>
              <ul>
                <li>
                  <button onClick={() => setActiveSection('Artifacts')}>Artifacts</button>
                </li>
                <li>
                  <button onClick={() => setActiveSection('Development')}>Spells</button>
                  {activeSection === 'Development' && (
                    <aside>
                      <ul>
                        <li>BackEnd</li>
                        <li>FrontEnd</li>
                        <li>CI/CD</li>
                      </ul>
                    </aside>
                  )}
                </li>
                <li><button onClick={() => setActiveSection('Lore')}>Lore</button></li>
                <li><button onClick={() => setActiveSection('About Wizard')}>About Wizard</button></li>
              </ul>
            </aside>
            {activeSection === null && (
              <WelcomePost />
            )}
            {activeSection === 'Development' && (
              <PostSection />
            )}
            {activeSection === 'Artifacts' && (
              <Artifacts />
            )}
            {activeSection === 'About Wizard' && (
              <Wizard />
            )}
          </div>
        </div>
      )}
    </>
  );
};

export default Grimoire;